<template>
  <div class="position-relative h-100">
    <div id="container" class="h-100" tabindex="0"></div>
    <!-- origin -->
    <Amaplayout v-show="originActive" :type="'origin'" :data="{city:'湛江',placeholder:'从那里出发'}" @oncancel="originActive=false" @onsearch="onsearch">
      <Listview v-for="(item,index) in searchdata.origin" :key="index" :data="item" :type="'itt'" @click="setlocation('origin',item)"/>
    </Amaplayout>
    <!-- destination -->
    <Amaplayout v-show="destinationActive" :type="'destination'" :data="{city:'湛江',placeholder:'你要去那儿'}" @oncancel="destinationActive=false" @onsearch="onsearch">
      <Listview v-for="(item,index) in searchdata.destination" :key="index" :data="item" :type="'itt'" @click="setlocation('destination',item)"/>
    </Amaplayout> 
    <div class="position-absolute start-0 end-0 bottom-0 " style="z-index:999;background-image: linear-gradient(to bottom, rgba(255,255,255,0), rgba(255,255,255,0),#f8f9fa 100px);">
      <div class="shadow-sm rounded bg-white m-2  p-3 d-grid gap-2">
        <Amap-origin :data="namedata.origin" :group="{placeholder:'..'}" @isActive="originActive=true"/>  
        <Amap-destination :data="namedata.destination" :group="{placeholder:'请输入市内目的地'}" @isActive="destinationActive=true"/>      
      </div>
    </div>
  </div>
</template>
<script>
import "@/router/ysdt.js"
import Amaplayout from '../layout/layout-amap'
import Listview from '../list/is'
import AmapOrigin from '../form/enum/amap-origin'
import AmapDestination from '../form/enum/amap-destination'
import Bkshadow from '@/bp/bk-shadow'
export default {
  components: {
    Amaplayout,
    Listview,
    AmapOrigin,
    AmapDestination,
      Bkshadow,
  },
  props: {
    data:{}
  },
  data() {
    return {
      form:{},
      originActive:false,
      destinationActive:false,
      searchdata:{origin:[],destination:[]},
      namedata:{
        origin:{
          name:'',
          value:'',
        },
        destination:{
          name:'',
          value:'',
        }
        },
    };
  },
  mounted(){
    ysdt._this=this;
    ysdt.install();
    //ysdt.drivinginstall('110.302534,21.138619', '110.256329,21.370257');
  },
  methods:{
    onsubmit(){
      for(let x in this.data.data){
      this.form[x]=this.data.data[x].value;
      }
      let maps_value=document.getElementById('maps_value').value;
      if(maps_value && this.form.destination){
      this.$router.push({path:this.$route.path+this.data.url,query:{"origin":maps_value,"destination":this.form.destination}});
      }
    },
    setlocation(name, data){
      if(name==="origin"){
        ysdt.setcenter(data.value)
        this.originActive=false;
      }else if(name==="destination"){
        this.destinationActive=false;
      }
      this.getnamedata(name, data);
    },
    getsearch(name,data){
      this.searchdata[name]=data;
    },
    getnamedata(name,data){
      this.namedata[name]=data;
      if(this.namedata.origin.value && this.namedata.destination.value){
        this.$router.push({ path: 'passenger/riding', query: { origin: this.namedata.origin.value,originname: this.namedata.origin.name,destination: this.namedata.destination.value,destinationname: this.namedata.destination.name }})
      //ysdt.drivinginstall(this.namedata.origin.value, this.namedata.destination.value);
      }
    },
    onsearch(name){
      if(name==="origin"){
        ysdt.originsearch();
      }else if(name==="destination"){
        ysdt.destinationsearch();
      }
    }
  }
}
</script>


