<template>
  <div class="rounded p-2 d-flex align-items-center" @click="$emit('isActive')">
    <div class="d-inline bg-primary rounded-circle p-1 ms-2"><div class="bg-white rounded-circle p-1"></div></div>
    <div class="ps-2" v-if="data.name">你将从<span class="text-primary fw-lighter">{{data.name}}</span>上车</div>
    <div class="ps-2" v-else>{{group.placeholder}}</div>
  </div> 
</template>
<script>
export default {
  components: {
  },
  props: {
    group:'',
    data:{}
  },

}
</script>



