<template>
  <div>
    <div ref="head" v-if="data.head">
      <Layout-head :data="data.head"/>
    </div>
    <div class="overflow-auto" :style="{height:bodyheight+'px'}">
      <my-scroll :page="page" :on-refresh="onRefresh" :on-pull="onPull">
         <View-template v-for="(item,key) in data.data" :key="key" :data="item" />
      </my-scroll>
      <slot></slot>
    </div>
    <div ref="foot" v-if="data.foot">
      <Layout-foot :data="data.foot"/>
    </div>
  </div>
</template>

<script>
import LayoutAuto from "./layout-auto";
import ViewTemplate from "@/components/group/template";
import LayoutHead from "./head/is";
import LayoutFoot from "./foot/is";
import myScroll from '@/bp/layout-scroll'
export default {
  components: {
      LayoutAuto,
      ViewTemplate,
      LayoutHead,
      LayoutFoot,
      myScroll,
  },
  props:{
    data:{}
  },
  data(){
        return {
            bodyheight:0,
            headheight:0,
            footheight:0,
            list:[],
            page:{
                counter:1,  
                pageStart:1,  
                pageEnd:1,  
                total:1
            },
            loading:true,
        }
  },
  mounted(){
    if(this.data.head){
    this.headheight=this.$refs.head.offsetHeight;
    }
    if(this.data.foot){
    this.footheight=this.$refs.foot.offsetHeight;
    }
    this.bodyheight=window.innerHeight-this.headheight-this.footheight;
    const _this = this;
    // 监听浏览器窗口变化
    window.onresize = function() {
      return (() => {
        _this.bodyheight = window.innerHeight-_this.headheight-_this.footheight;
      })();
    };
     this.pathaxios('refresh');
  },
    methods:{
        onRefresh(mun){ //刷新回调
            this.page.counter=1;
            this.pathaxios('refresh');
        },
        onPull(mun){ //加载回调
            if(this.page.counter<this.page.total){
                 this.page.counter=2;
                 this.pathaxios('pull');
            }else{
                this.$root.$emit('setState',7)
            }
        },
        pathaxios(loadkey){
           if(this.loading){
           this.loading=false;
           this.data.url="/index/layout/class/prorder_show-1";
           this.$http.post(this.$store.state.apiset.url+this.data.url+'?token='+this.$store.state.token+'&page='+this.page.counter,{msg:'list'})
            .then(response => {
                let res=response.data;
                this.loading=true;
               if(res.code==1){
                    res=res.data;
                    this.data.data=res.data;
                   // console.log(res.data);
                   if(res.page){
                    this.page=res.page;
                    }
                    if(loadkey=="refresh"){
                            this.list=res.data;
                            this.$root.$emit('setState',3)
                    }else if(loadkey=="pull"){
                            for(let index in res.data){
                            this.list.push(res.data[index])
                            }
                            this.$root.$emit('setState',5)
                    }else{
                            this.list=res.data;
                    }
               }else{

               }
            }).catch(function (error) {this.loading=true;console.log('请求失败！')})
            }
        }, 
    },
}
</script>



