<template>
  <div class="position-fixed top-0 start-0 end-0 bottom-0" style="z-index:99999">
    <div ref="head" class="p-2 bg-white position-relative border-bottom">
        <div class="pe-5">
            <div class="input-group input-group-sm">
                <span class="input-group-text">{{data.city}}</span>
                <input :id="'amap-'+type+'-city'" type="hidden" :value="data.city">
                <input class="form-control" :id="'amap-'+type" value="" :placeholder="data.placeholder" />
            </div>
        </div>
        <div class="position-absolute top-0 end-0 p-2"><div  @click="$emit('oncancel')">取消</div></div>
    </div>
    <div class="bg-light overflow-auto" :style="{height:bodyheight+'px'}">
        <slot></slot>
      </div>
    <div ref="foot">
    </div>
  </div>
</template>

<script>
export default {
  components: {
  },
  props:{
    type:'',
    data:{}
  },
  data(){
        return {
            bodyheight:0,
            headheight:0,
            footheight:0,
        }
  },
  mounted(){
    this.headheight=this.$refs.head.offsetHeight;
    this.footheight=this.$refs.foot.offsetHeight;
    this.bodyheight=window.innerHeight-this.headheight-this.footheight;
    const _this = this;
    // 监听浏览器窗口变化
    window.onresize = function() {
      return (() => {
        _this.bodyheight = window.innerHeight-_this.headheight-_this.footheight;
      })();
    };
    
  },
  methods:{
 
  }
}
</script>



